
.craft-notification {
  font-size: 12px !important; /* ✅ Force override */
  text-shadow: 0.5px 0.5px 4px rgba(0, 0, 0, 0.3); /* ✅ Softer, smaller shadow */

}

/* Ensure the body and root elements are styled correctly */
body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
  background-color: #f5f5f5;
}

/* Base Layout */
.app-container {
  display: flex;
  height: 100vh;
}

/* Base Panel */
.base-panel {
  width: 180px;
  background-color: #67c257;
  padding: 10px;
  font-size: 14px;
  box-shadow: 2px 0 5px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  height: 100vh;
  position: fixed;
  z-index: 150;
}

.base-panel h1 {
  margin: 0;
  padding: 0;
  font-size: 1.5em;
  color: white;
  text-align: left;
}

.base-panel h2 {
  margin: 0;
  padding: 0;
  font-size: 1.4em;
  color: white;
  text-align: left;
}

.base-panel h3 {
  margin: 0;
  padding: 0;
  font-size: 1em;
  color: rgb(0, 104, 12);
  text-align: left;
}

.base-panel h4 {
  margin: 0;
  padding: 0;
  font-size: 0.9em;
  color: rgb(255, 255, 255);
  text-align: left;
}

.base-panel p {
  margin: 0;
  padding: 0;
  font-size: 12px;
  color: rgb(0, 104, 12);
  text-align: left;
}

.money-value {
  color: white; /* ✅ Makes the money value appear in white */
}

/* Shared Button Styles */
.shared-button {
  background-color: #fff;
  color: #4caf50;
  border: none;
  border-radius: 4px;
  padding: 5px 10px;
  font-size: 14px;
  cursor: pointer;
  margin: 5px 0; /* Add spacing between buttons */
  text-align: center;
}

.shared-button:hover {
  background-color: #f1f1f1;
}

.shared-button:active {
  background-color: #e0e0e0;
}

.base-panel .shared-button {
  width: 80%; /* Ensure buttons fit well in the base panel */
  text-align: left; /* Align text for a different look in base panel */
}

.app-header .shared-button {
  width: auto; /* Keep header buttons compact */
}

.reset-button {
  background-color: #ff5252; /* Bright Red */
  color: white;
  font-weight: bold;
  padding: 8px;
  margin-top: 10px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.reset-button:hover {
  background-color: #ff0000;
}

/* Header */
.app-header {
  background-color: #67c257;
  color: white;
  display: flex;
  justify-content: space-between;
  padding: 10px;
  position: fixed;
  top: 0;
  left: 200px; /* Adjust for base panel width */
  width: calc(100% - 180px); /* Exclude base panel width */
  z-index: 150;
  white-space: nowrap; /* ✅ Prevent text from wrapping */

}

.header-controls {
  display: flex;
  align-items: center;
  gap: 5px;
}

.header-controls p {
  font-size: 12px;
  color: white;
}


/* Homestead */
.homestead {
  overflow: auto;
  margin-left: 200px;
  width: calc(100% - 200px);
  height: calc(100vh - 100px); /* Adjust for header and status bar */
  position: absolute;
  top: 85px;
  z-index: 100;
}

/* Player Icon */
.player {
  position: relative;
  z-index: 50;
}

.range-indicator {
  position: absolute;
  border-radius: 50%;
  pointer-events: none; /* ✅ Ensures it doesn't interfere with gameplay */
  transition: left 0.1s linear, top 0.1s linear; /* ✅ Smooth movement */
}


/* NPCs */
.npc {
  cursor: pointer; /* Makes the mouse pointer appear as a clickable hand */
  z-index: 50; /* Ensures NPCs are above tiles and other background elements */
}

/* Modals */
.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal-container {
  background-color: white;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2);
  width: 90%;
  max-width: 400px;
  position: relative;
}

.modal-close-btn {
  position: absolute;
  top: 10px;
  right: 10px;
  background: transparent;
  border: none;
  color: #444;
  font-size: 1.5rem;
  cursor: pointer;
}

.modal-close-btn:hover {
  color: #f44336;
}

/* Tooltips */
.tooltip-container {
  position: absolute;
  pointer-events: none;
  background: rgba(32, 32, 32, 0.85);
  color: white;
  padding: 8px;
  border-radius: 5px;
  font-size: 12px;
  z-index: 3000;
  white-space: nowrap;
  max-width: 250px;
  transform: translateX(-50%);
}

.tooltip-container p {
  margin: 0;
}

.tooltip-pc-container {
  position: absolute;
  pointer-events: none;
  background: rgba(0, 75, 11, 0.85);
  color: white;
  padding: 8px;
  border-radius: 5px;
  font-size: 12px;
  z-index: 3000;
  white-space: nowrap;
  max-width: 250px;
  transform: translateX(-50%);
}

.tooltip-pc-container p {
  margin: 0;
}

/* Animations */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translate(-50%, -90%);
  }
  to {
    opacity: 1;
    transform: translate(-50%, -100%);
  }
}

/* Inventory Content */
.inventory-content {
  max-height: 300px;
  overflow-y: auto;
  margin-top: 10px;
}

.inventory-content ul {
  list-style: none;
  padding: 0;
}

.inventory-content li {
  padding: 5px;
  background-color: #f5f5f5;
  margin-bottom: 5px;
  border-radius: 4px;
  border: 1px solid #ddd;
}

.inventory-content p {
  color: #555;
  text-align: center;
}

/* Tile Colors */

/* Default grid tile styles */
.tile-g,  /* grass */
.tile-s,  /* slate */
.tile-d,  /* dirt */
.tile-w,  /* water */
.tile-l,  /* lava */
.tile.p,  /* pavement */
.tile-n   /* sand */
{
  width: 100%;
  height: 100%;
  position: absolute;
  border: 1px solid #958f8f;
}

.tile-g { background-color: #3dc43d; }
.tile-s { background-color: #8b989c; }
.tile-d { background-color: #c0834a; }
.tile-w { background-color: #58cad8; }
.tile-l { background-color: #c4583d; }
.tile-p { background-color: #c5a85d; }
.tile-n { background-color: #fbde00; }
.tile-unknown {
  background-color: #ff0000; /* A visible color for debugging */
}
