/* Panels */
.panel-container {
    position: fixed;
    top: 0;
    left: 0px; /* Overlap with base panel */
    height: 100%;
    width: 200px; /* Fixed width for panels; overlaps base panel */
    background-color: white;
    box-shadow: 4px 0 8px rgba(0, 0, 0, 0.2);
    z-index: 1000;
    display: flex;
    flex-direction: column;
    overflow-y: auto;
  }
  
/* Fixed Panel Header (Title + Close Button) */
.panel-header {
  position: sticky;
  top: 0;
  background-color: white;
  padding: 5px;
  z-index: 101;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid #ddd;
  height: 35px; /* Fixed height for consistency */
}

/* Panel Title */
.panel-title {
  font-size: 16px;
  font-weight: bold;
  margin: 0;
  padding: 0;
  flex-grow: 1; /* Allows title to expand while keeping the close button aligned */
}

/* Scrollable Panel Content */
.panel-content {
  padding: 10px;
  margin: 0;
  font-size: 12px;
  flex: 1;
  overflow-y: auto; /* Allows scrolling */
}

/* Reduce vertical spacing for headings and paragraphs inside the Courthouse panel */
.panel-content h1 {
  margin-top: 0px; /* Reduce space above */
  margin-bottom: 2px; /* Reduce space below */
}
.panel-content h2,
.panel-content h3 {
  margin-top: 4px; /* Reduce space above */
  margin-bottom: 4px; /* Reduce space below */
}

  
/* Panel Close Button */
.panel-close-btn {
  background: transparent;
  border: none;
  color: #444;
  font-size: 1.5rem;
  cursor: pointer;
}

.panel-close-btn:hover {
  transform: scale(1.05);
  color: #f44336;
}

  /* Button list layout */
.panel-buttons {
  display: flex;
  flex-direction: column;
  gap: 4px;
  margin-top: 10px;
}

/* General button styling */
.panel-buttons button {
  padding: 8px 12px;
  border: none;
  border-radius: 5px;
  font-size: 12px;
  cursor: pointer;
  transition: background-color 0.3s, transform 0.2s;
}

/* Success buttons (green) */
.panel-buttons .btn-success {
  background-color: #4caf50;
  color: white;
}

.panel-buttons .btn-success:hover {
  background-color: #45a049;
  transform: scale(1.05);
}

/* Danger buttons (red) */
.panel-buttons .btn-danger {
  background-color: #d32f2f;
  color: white;
}

.panel-buttons .btn-danger:hover {
  background-color: #c62828;
  transform: scale(1.05);
}

/* Neutral buttons (default gray) */
.panel-buttons .btn-neutral {
  background-color: #9e9e9e;
  color: white;
}

.panel-buttons .btn-neutral:hover {
  background-color: #757575;
  transform: scale(1.05);
}


/* Panel Description */

  .panel-description {
    margin-top: 20px;
    padding: 10px;
    border-top: 1px solid #ccc;
    font-size: 12px;
    color: #666;
    text-align: left;
  }


